<template>
  <div class="personal" ref="presonal">
    <!-- 头部登录组件 -->
    <div class="personalHeader" @click="toLogin">
      <img v-if="userDetail.profile" :src="userDetail.profile.avatarUrl" />
      <span class="iconfont icon-user" v-else></span>
      <div class="userName">
        <p>
          {{ userDetail.profile ? userDetail.profile.nickname : '立即登录' }}
        </p>
        <span class="level" v-if="userDetail.level"
          >Lv.{{ userDetail.level }}</span
        >
      </div>
      <span
        :class="
          userDetail.profile ? 'iconfont icon-you right' : 'iconfont icon-you'
        "
      ></span>
    </div>
    <!-- 功能导航模块 -->
    <div class="personalNav">
      <div class="personalNavItem">
        <span class="iconfont icon-musicicon"></span>
        <span>本地/下载</span>
      </div>
      <div class="personalNavItem">
        <span class="iconfont icon-user-group"></span>
        <span>我的好友</span>
      </div>
      <div class="personalNavItem">
        <span class="iconfont icon-starmarkhighligh"></span>
        <span>收藏和赞</span>
      </div>
      <div class="personalNavItem">
        <span class="iconfont icon-a-shipinti1beifen3"></span>
        <span>我的播客</span>
      </div>
      <div class="personalNavItem">
        <span class="iconfont icon-rili"></span>
        <span>本月爱听</span>
      </div>
      <div class="personalNavItem">
        <span class="iconfont icon-plus"></span>
        <span>音乐应用</span>
      </div>
    </div>
    <!-- 我喜欢的音乐歌单 -->
    <router-link
      class="personalLately isFlex"
      v-if="userCreateList[0]"
      :to="{
        path: '/listView',
        query: { id: userCreateList[0].id, scrollV: scrollV },
      }"
    >
      <img class="likeMusicImg" :src="userCreateList[0].coverImgUrl" alt />
      <div class="likeMusic">
        <p>我喜欢的音乐</p>
        <span>{{ userCreateList[0].trackCount }} 首</span>
      </div>
    </router-link>
    <!-- 创建的歌单 -->
    <div class="personalLately" v-if="userDetail.profile">
      <span class="latelyText"
        >创建歌单（{{ userCreateList.length - 1 }}个）</span
      >
      <div class="iconRight">
        <span class="iconfont icon-plus"></span>
        <span class="iconfont icon-gengduo"></span>
      </div>
      <router-link
        class="isFlex isTop"
        v-for="(item, index) in userCreateList.slice(1)"
        :to="{
          path: '/listView',
          query: { id: item.id, scrollV: scrollV },
        }"
        :key="index"
      >
        <img class="likeMusicImg" :src="item.coverImgUrl" alt />
        <div class="likeMusic">
          <p>{{ item.name }}</p>
          <span>{{ item.trackCount }} 首</span>
        </div>
      </router-link>
    </div>
    <!-- 收藏的歌单 -->
    <div class="personalLately" v-if="userDetail.profile">
      <span class="latelyText">收藏歌单（{{ userMusicList.length }}个）</span>
      <div class="iconRight">
        <span class="iconfont icon-plus"></span>
        <span class="iconfont icon-gengduo"></span>
      </div>
      <router-link
        class="isFlex isTop"
        v-for="(item, index) in userMusicList"
        :to="{
          path: '/listView',
          query: { id: item.id, scrollV: scrollV },
        }"
        :key="index"
      >
        <el-image class="likeMusicImg" :src="item.coverImgUrl" alt lazy />
        <div class="likeMusic">
          <p>{{ item.name }}</p>
          <span>{{ item.trackCount }} 首</span>
        </div>
      </router-link>
    </div>
    <!-- 其他功能模块 -->
    <div class="personalLately">
      <span class="latelyText">其他</span>
      <div class="moreItemList">
        <div class="moreItem">
          <span class="iconfont icon-xingzhuang9"></span>
          <span class="moreItemText">设置</span>
          <span class="iconfont icon-you"></span>
        </div>
        <div class="moreItem">
          <span class="iconfont icon-yifu"></span>
          <span class="moreItemText">个性装扮</span>
          <span class="iconfont icon-you"></span>
        </div>
        <div class="moreItem">
          <span class="iconfont icon-fuwudingdanxiangqing-kefu"></span>
          <span class="moreItemText">我的客服</span>
          <span class="iconfont icon-you"></span>
        </div>
        <div class="moreItem">
          <span class="iconfont icon-guanyu1"></span>
          <span class="moreItemText">关于</span>
          <span class="iconfont icon-you"></span>
        </div>
      </div>
    </div>
    <!-- 退出登录 -->
    <button v-if="userDetail.profile" class="confirmBtn" @click="backLogin">
      退出登录
    </button>
    <el-backtop :bottom="120" />
    <tab-bar />
  </div>
</template>
<script>
import tabBar from '@/components/common/tabBar.vue'
import { mapMutations, mapState } from 'vuex'
import { ElMessageBox } from 'element-plus'
import {
  getUserMusicList,
  getLikedSong,
  Logout,
  refreshLogin,
} from '@/tools/request'

export default {
  name: 'Personal',
  data() {
    return {
      userDetail: [],
      userCreateList: [],
      userMusicList: [],
      scrollV: 0,
    }
  },
  components: {
    tabBar,
  },
  activated() {
    this.$nextTick(() => {
      if (localStorage.user) {
        let user = JSON.parse(localStorage.user)
        this.userDetail = user
        this.reqMusicList()
        this.reqLikedSong()
      }
    })
    setTimeout(() => {
      document.body.scrollTop = this.scrollV
      document.documentElement.scrollTop = this.scrollV
      window.addEventListener('scroll', this.handleScroll, true)
    }, 300)
  },
  deactivated() {
    window.removeEventListener('scroll', this.handleScroll, true)
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll() {
      this.scrollV =
        document.documentElement && document.documentElement.scrollTop
    },
    backLogin() {
      ElMessageBox.confirm('确定要退出登录吗？', {
        confirmButtonText: '退出',
        confirmButtonClass: 'confirm',
        cancelButtonText: '取消',
        type: 'info ',
      })
        .then(() => {
          this.logout()
          localStorage.removeItem('user')
          localStorage.removeItem('userLikeSongId')
          this.$router.go(0)
        })
        .catch(() => {})
    },
    async logout() {
      let result = await Logout()
      if (result.data.code == 200) {
        let result1 = await refreshLogin()
        if (result1.data.code == 200) {
          // console.log('退出登录成功');
        }
      }
    },
    toLogin() {
      if (!this.userDetail.profile) {
        this.$router.push('/login')
      }
    },
    // 请求用户歌单
    async reqMusicList() {
      let result = await getUserMusicList(this.userDetail.profile.userId)
      let userCreateList = []
      let userlist = []
      for (let i = 0; i < result.data.playlist.length; i++) {
        if (result.data.playlist[i].subscribed === false) {
          userCreateList.push(result.data.playlist[i])
        } else {
          userlist.push(result.data.playlist[i])
        }
      }
      this.userCreateList = userCreateList
      this.userMusicList = userlist
    },
    // 请求用户喜欢的歌曲id
    async reqLikedSong() {
      let result = await getLikedSong(this.userDetail.profile.userId)
      localStorage.setItem('userLikeSongId', result.data.ids)
    },
  },
  computed: {
    ...mapState(['user']),
  },
}
</script>
<style lang="less" scoped>
/deep/.el-image {
  display: block !important;
  overflow: visible !important;
}
/deep/.el-image__inner {
  width: 1rem !important;
  height: 1rem !important;
  border-radius: 10px;
  filter: grayscale(40%);
}
.isTop {
  margin-top: 0.2rem;
}
.iconRight {
  display: flex;
  align-items: center;
  float: right;
  color: #999;
  .iconfont:first-child {
    font-size: 0.25rem;
    margin-right: 0.4rem;
  }
}
.personal {
  width: 7.5rem;
  background-color: #f9f9f9;
  background-image: linear-gradient(#e7e7e7, #fff, #fff, #fff);
  padding-bottom: 2.9rem;
  // 头部登录组件
  .personalHeader {
    display: flex;
    height: 1.8rem;
    align-items: center;
    margin: 0 0.4rem;
    .icon-user {
      font-size: 0.6rem;
      color: #f0dbda;
      background-color: #f6f2f1;
      margin-right: 0.2rem;
      padding: 0.25rem;
      border: 2px solid #fff;
      border-radius: 50%;
    }
    p {
      color: #333;
      font-weight: 500;
      font-size: 0.32rem;
    }
    .icon-you {
      font-size: 0.2rem;
      margin: 2px 0 0 0.1rem;
    }
    .right {
      flex: 1;
      display: flex;
      justify-content: flex-end;
    }
    img {
      width: 1rem;
      height: 1rem;
      margin-right: 0.2rem;
      border: 2px solid #fff;
      border-radius: 50%;
    }
    .level {
      font-size: 10px;
      background-color: #fff;
      padding: 2px 8px;
      border-radius: 10px;
    }
  }
  // 功能导航模块
  .personalNav {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    align-content: center;
    background-color: #fff;
    margin: 0 0.3rem 0.3rem 0.3rem;
    padding: 0.6rem 0.4rem;
    box-shadow: 0 0 20px 0px rgba(151, 151, 151, 0.3);
    border-radius: 0.25rem;
    .personalNavItem {
      display: flex;
      flex-direction: column;
      width: 25%;
      .iconfont {
        font-size: 0.5rem;
        color: #d43c33;
        margin-bottom: 5px;
      }
      span {
        color: #777;
        font-size: 0.24rem;
      }
    }
    .personalNavItem:last-child .iconfont {
      font-size: 0.2rem;
      background-color: #f8f8f8;
      padding: 10px 0;
      margin: 0 25px;
      margin-bottom: 1px;
      border-radius: 50%;
      color: #adadad;
    }
    .personalNavItem:nth-child(n + 5) {
      margin-top: 30px;
    }
  }
  // 其他功能模块
  .personalLately {
    background-color: #fff;
    margin: 0 0.3rem 0.3rem 0.3rem;
    padding: 0.3rem;
    box-shadow: 0 0 20px 0px rgba(151, 151, 151, 0.3);
    border-radius: 0.25rem;
    .latelyText {
      font-size: 0.2rem;
      color: #999;
    }
    .moreItem {
      margin: 0.5rem 0;
      color: #555;
      .icon-you {
        float: right;
        font-size: 0.3rem;
        color: #ccc;
      }
      .moreItemText {
        font-size: 0.25rem;
      }
      span:first-child {
        font-size: 0.28rem;
        margin-right: 0.3rem;
      }
    }
    .moreItem:first-child {
      margin-top: 0.2rem;
    }
    .moreItem:last-child {
      margin-bottom: 0.2rem;
    }
  }
  // 我喜欢的音乐歌单
  .isFlex {
    display: flex;
    .likeMusicImg,
    .el-skeleton__image {
      width: 1rem;
      height: 1rem;
      border-radius: 10px;
      filter: grayscale(40%);
    }
    .likeMusic {
      margin: 0.1rem 0 0 0.15rem;
      p {
        font-size: 0.28rem;
        letter-spacing: 2px;
        white-space: normal;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
      }
      span {
        font-size: 0.25rem;
        color: #888;
      }
    }
  }
  // 退出登录
  .confirmBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    height: 0.8rem;
    background-color: #d43c33;
    margin: 0.5rem auto 0 auto;
    border-radius: 1rem;
    border: none;
    font-size: 0.25rem;
    transition: all 0.3s;
    color: #fff;
  }
  .confirmBtn:active {
    opacity: 0.9;
  }
}
</style>
